import { useState, useEffect } from "react";

const useNow = (interval = 1234) => {
  const [now, setNow] = useState(Date.now());

  useEffect(() => {
    
    // effect
    const int = setInterval(() => {
      setNow(Date.now());
    }, interval);

    return () => {
      // cleanup
      clearInterval(int);
    };
    
  }, [interval]);

  return now;
};

export default useNow;
