import React, { useContext } from "react";
import { AppContext } from "../App/App";

function Button({ handleClick, classes, text }) {
  const ctx = useContext(AppContext);
  return (
    <button
      type="button"
      className={classes}
      onClick={handleClick}
    >
      {text}
    </button>
  );
}

Button.defaultProps = {
  handleClick: (evt) => {},
};

export default Button;
